import React, { useState } from "react";

const GoalItem = ({
  id,
  description,
  progress,
  done = false,
  onEditItem,
  onChangeItemStatus,
  onDeleteItem,
  onSelectItem,
  onAddTasks = null,
  isTask = false,
  isAddTask = false,
  isProject = false,
  selected = false,
  parent = null,
}) => {
  const [showDetails, setShowDetails] = useState(false);
  return (
    <li className="goals__item">
      <div className="goals__main">
        {isTask ? (
          <span
            className="goals__progress goals__progress--hover"
            onClick={onChangeItemStatus}
          >
            {done ? (
              <img
                src={require("./../../public/assets/icons/check-mark-1-64.png")}
                alt="Opcoes"
                className="goals__status"
              />
            ) : (
              <img
                src={require("./../../public/assets/icons/circle-with-minus.svg")}
                alt="Opcoes"
                className="goals__status"
              />
            )}
          </span>
        ) : (
          <span className="goals__progress">{progress}%</span>
        )}

        <span
          className={`goals__description ${
            isTask ? "goals__description--task-hover" : ""
          } ${done ? "goals__description--strike" : ""}`}
          onClick={onChangeItemStatus}
        >
          {description}
        </span>
        <span className="goals__parent">{parent}</span>
        <button type="button" className="btn-image goals__options">
          {isAddTask ? (
            <img
              src={require("./../../public/assets/icons/check-mark-white.png")}
              alt="Opcoes"
              onClick={onSelectItem}
              className={`goals__check ${
                selected ? "goals__check--selected" : ""
              }`}
            />
          ) : (
            <img
              src={require("./../../public/assets/icons/dots-three-vertical.svg")}
              alt="Opcoes"
              onClick={() => setShowDetails((value) => !value)}
            />
          )}
        </button>
      </div>
      <div
        className={`goals__actions ${
          showDetails ? "goals__actions--show" : ""
        }`}
      >
        <button
          className="goals__actions-item goals__actions-item--add-tasks btn-image"
          onClick={onAddTasks}
        >
          <img
            src={require("./../../public/assets/icons/clipboard-6.svg")}
            alt="Adicionar tarefas"
          />
        </button>

        <button
          className="goals__actions-item goals__actions-item--edit btn-image"
          onClick={onEditItem}
        >
          <img
            src={require("./../../public/assets/icons/edit.svg")}
            alt="Editar"
          />
        </button>
        <button className="goals__actions-item goals__actions-item--remove btn-image">
          <img
            src={require("./../../public/assets/icons/trash.svg")}
            alt="Remover"
            onClick={onDeleteItem}
          />
        </button>
      </div>
    </li>
  );
};

export default GoalItem;



