import React, { useLayoutEffect, useState, useEffect } from "react";
import CircularProgressBar from "./../../components/circular-progress-bar.component";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  changeTaskStatus,
  deleteTask,
} from "../../redux/actions/tasks.actions";
import modalUtil from "../../utils/modal.util";
import { updateProgress } from "../../redux/actions/lists.actions";
import { calculateProgress } from "../../utils/calculate-progress.util";
import { useNavigate } from "react-router";
import GoalItem from "../../components/goal-item.component";
import { updateProjectProgress } from "./../../redux/actions/projects.actions";

const HomePage = () => {
  const lists = useSelector((state) =>
    state.lists.lists.filter((list) => list.isVisible && list.type == "action")
  );

  const [listId, setListId] = useState(lists[0] ? lists[0].id : "");

  const [percentage, setPercentage] = useState(0);

  const tasks = useSelector((state) =>
    state.tasks.tasks.filter((task) => task.listId == listId)
  );

  let selectedList = useSelector((state) =>
    state.lists.lists.find((list) => list.id == listId)
  );

  useLayoutEffect(() => {
    document.title = "Proximas Accoes";
  });

  useEffect(() => {
    if (selectedList) setPercentage(selectedList.progress);
  });

  const dispatch = useDispatch();

  const navigate = useNavigate();

  const handleEdit = (id) => {
    navigate(`/task/update/${id}`);
  };

  const handleDelete = (id) => {
    const title = "Confirmacao Remocao";

    const msg = "Tem certeza que deseja remover esta tarefa?";

    modalUtil.showModal(title, msg, () => {
      dispatch(deleteTask(id));
      const progress = calculateProgress(listId, tasks);
      dispatch(updateProgress(listId, progress));
    });
  };

  const handleChangeStatus = (id) => {
    dispatch(changeTaskStatus(id));

    const progress = calculateProgress(listId, tasks, null);

    dispatch(updateProgress(listId, progress));
  };

  return (
    <React.Fragment>
      <div className="overview overview--three">
        <CircularProgressBar
          percentage={percentage}
          title={selectedList.designation}
        />
      </div>
      <div className="container">
        <Link to="/task/create" className="btn btn--primary btn--block">
          Adicionar tarefa
        </Link>

        <form className="form mt-2">
          <div className="form__group">
            <label htmlFor="list" className="form__label">
              Lista
            </label>
            <select
              value={listId}
              onChange={(e) => setListId(e.target.value)}
              name="list"
              id="list"
              className="form__select"
            >
              <option value="">-- Selecione uma lista --</option>
              {lists.map((list, i) => (
                <option key={i} value={list.id}>
                  {list.designation}
                </option>
              ))}
            </select>
          </div>
        </form>
        <ul className="goals__list">
          {tasks.map((task, j) => {
            return (
              <GoalItem
                key={j}
                id={task.id}
                description={task.description}
                progress={task.done}
                onEditItem={() => handleEdit(task.id)}
                onDeleteItem={() => handleDelete(task.id)}
                onChangeItemStatus={() => handleChangeStatus(task.id)}
                isTask={true}
                done={task.done}
              />
            );
          })}
        </ul>
      </div>
    </React.Fragment>
  );
};

export default HomePage;



