import React from "react";
import { Routes, Route } from "react-router-dom";

import TopbarLayout from "./layouts/topbar.layouts";
import { useSelector } from "react-redux";
import MainLayout from "./layouts/main.layout";

import HomePage from "./pages/home/home.page";
import AddTaskPage from "./pages/home/add-task.page";
import LifeFieldsPage from "./pages/lifeFields/index.page";
import ListsPage from "./pages/lists/index.page";
import CreateListsPage from "./pages/lists/create.page";
import CreateLifeFieldPage from "./pages/lifeFields/create.page";
import AnualGoalsPage from "./pages/anual-goals/index.page";
import AnualCronology from "./pages/anual-goals/anual-cronology";
import CreateAnualGoal from "./pages/anual-goals/create.page";
import MonthlyGoalsPage from "./pages/monthly-goals/index.page";
import CreateMonthlyGoal from "./pages/monthly-goals/create.page";
import ProjectsPage from "./pages/projects/index.page";
import CreateProjectPage from "./pages/projects/create.page";
import ProjectTasksPage from "./pages/projects/project-tasks.pages";

import SignInPage from "./pages/auth/signin.page";
import SignUpPage from "./pages/auth/signup.page";
import ForgotPasswordPage from "./pages/auth/forgot-password.page";
import ResetTokenPage from "./pages/auth/reset-token.page";
import PasswordResetPage from "./pages/auth/password-reset.page";
import InboxPage from "./pages/inbox/index.page";
import AddToInbox from "./pages/inbox/add-to-inbox.page";

import OverlayLayout from "./layouts/overlay.layouts";
import SidebarLayout from "./layouts/sidebar.layouts";
import Spinner from "./components/spinner.component";
import Modal from "./components/modal.component";

const App = () => {
  //const currentUser = useSelector((state) => state.user.currentUser);

  const currentUser = true;

  return (
    <React.Fragment>
      <TopbarLayout />
      <SidebarLayout />
      <OverlayLayout />
      <Spinner />
      <Modal />
      {currentUser ? (
        <Routes>
          <Route exact path="/" element={<MainLayout />}>
            <Route exact path="home" element={<HomePage />} />
            <Route exact path="task/create" element={<AddTaskPage />} />
            <Route exact path="task/update/:id" element={<AddTaskPage />} />

            <Route exact path="inbox" element={<InboxPage />} />
            <Route exact path="inbox/create" element={<AddToInbox />} />
            <Route exact path="inbox/update/:id" element={<AddToInbox />} />
            <Route exact path="anual-goals" element={<AnualGoalsPage />} />
            <Route exact path="monthly-goals" element={<MonthlyGoalsPage />} />
            <Route
              exact
              path="anual-goals/create"
              element={<CreateAnualGoal />}
            />
            <Route
              exact
              path="anual-goals/update/:id"
              element={<CreateAnualGoal />}
            />
            <Route
              exact
              path="anual-goals/cronology/:id"
              element={<AnualCronology />}
            />
            <Route
              exact
              path="monthly-goals/create"
              element={<CreateMonthlyGoal />}
            />
            <Route
              exact
              path="monthly-goals/create/:anualGoalId/:month"
              element={<CreateMonthlyGoal />}
            />
            <Route
              exact
              path="monthly-goals/update/:id"
              element={<CreateMonthlyGoal />}
            />
            <Route exact path="projects" element={<ProjectsPage />} />
            <Route
              exact
              path="projects/create"
              element={<CreateProjectPage />}
            />
            <Route
              exact
              path="projects/update/:id"
              element={<CreateProjectPage />}
            />
            <Route
              exact
              path="projects/add-tasks/:id"
              element={<ProjectTasksPage />}
            />
            <Route exact path="life-fields" element={<LifeFieldsPage />} />
            <Route
              exact
              path="create-life-field"
              element={<CreateLifeFieldPage />}
            />
            <Route
              exact
              path="update-life-field/:id"
              element={<CreateLifeFieldPage />}
            />
            <Route exact path="lists/" element={<ListsPage />} />
            <Route exact path="lists/create" element={<CreateListsPage />} />
            <Route
              exact
              path="lists/update/:id"
              element={<CreateListsPage />}
            />
          </Route>
        </Routes>
      ) : (
        <Routes>
          <Route exact path="/" element={<MainLayout />}>
            <Route exact path="" element={<SignInPage />} />
            <Route exact path="/signin" element={<SignInPage />} />
            <Route exact path="/signup" element={<SignUpPage />} />
            <Route
              exact
              path="/forgot-password"
              element={<ForgotPasswordPage />}
            />
            <Route exact path="/reset-token" element={<ResetTokenPage />} />
            <Route
              exact
              path="/password-reset/:token"
              element={<PasswordResetPage />}
            />
          </Route>
        </Routes>
      )}
    </React.Fragment>
  );
};

export default App;



