import React, { useLayoutEffect } from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import ListItem from "../../components/list-item.component";
import { useDispatch } from "react-redux";
import { deleteList } from "./../../redux/actions/lists.actions";
import modalUtil from "./../../utils/modal.util";
import { changeListVisibility } from "./../../redux/actions/lists.actions";

const ListsPage = () => {
  const listsList = useSelector((state) => state.lists.lists);

  useLayoutEffect(() => {
    document.title = "Listas";
  });

  const dispatch = useDispatch();

  const handleDelete = (id) => {
    const title = "Confirmar remocao";
    const msg = "Tem certeza que deseja remover este registo?";

    modalUtil.showModal(title, msg, () => {
      dispatch(deleteList(id));
    });
  };

  const handleChangeVisibility = (id) => {
    dispatch(changeListVisibility(id));
  };

  return (
    <div className="container">
      <Link to="/lists/create" className="btn btn--primary btn--block">
        Novo
      </Link>
      <ul className="list mt-2">
        {listsList.map((list, i) => {
          return (
            <ListItem
              key={i}
              {...list}
              onDelete={handleDelete}
              updateUrl="/lists/update"
              isVisible={list.isVisible}
              onChangeVisibility={() => handleChangeVisibility(list.id)}
            />
          );
        })}
      </ul>
    </div>
  );
};

export default ListsPage;



