import React, { useLayoutEffect, useState } from "react";
import spinnerUtil from "../../utils/spinner.util";
import API_URL from "../../config/api-config";
import { useNavigate } from "react-router-dom";

const ForgotPasswordPage = () => {
  const [email, setEmail] = useState("");

  const [errorEmail, setErrorEmail] = useState("");

  const navigate = useNavigate();

  useLayoutEffect(() => {
    document.title = "Reniciar minha senha";
  });

  const isFormValid = () => {
    let isValid = true;

    if (email === "") {
      setErrorEmail("Email e obrigatorio");
      isValid = false;
    } else {
      setErrorEmail("");
    }

    return isValid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!isFormValid()) return;

    spinnerUtil.showSpinner();

    try {
      const response = await fetch(`${API_URL}/users/forgot-password`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ email }),
      });

      const data = await response.json();

      if (data.status === "success") {
        navigate("/reset-token");
      } else {
        setErrorEmail(data.message);
      }
    } catch (error) {
      console.log(error);
    } finally {
      spinnerUtil.hideSpinner();
    }
  };
  return (
    <div className="container">
      <form className="form" onSubmit={handleSubmit}>
        <div className="form__group">
          <p className="p">
            Digite o endereço de e-mail verificado da sua conta de usuário e nós
            lhe enviaremos um link de redefinição de senha.
          </p>
        </div>

        <div className="form__group">
          <input
            type="email"
            className="form__input"
            placeholder="Digite o seu endereco de e-mail"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            aria-label="Digite o seu endereco de e-mail"
          />
          <span className="form__error">{errorEmail}</span>
        </div>

        <div className="form__group">
          <button type="submit" className="btn btn--primary form__btn">
            Envie-me o link para reniciar senha
          </button>
        </div>
      </form>
    </div>
  );
};

export default ForgotPasswordPage;



