import React, { useLayoutEffect, useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { addTask, updateTask } from "../../redux/actions/tasks.actions";
import { useParams, useNavigate } from "react-router";
import { updateProgress } from "../../redux/actions/lists.actions";
import { calculateProgress } from "../../utils/calculate-progress.util";

const AddToInbox = () => {
  const [listId, setListId] = useState("");
  const [description, setDescription] = useState("");

  const [errorListId, setErrorListId] = useState("");
  const [errorDescription, setErrorDescription] = useState("");
  const lists = useSelector((state) =>
    state.lists.lists.filter((list) => list.isVisible && list.type == "inbox")
  );

  const allTasks = useSelector((state) => state.tasks.tasks);

  const dispatch = useDispatch();

  const params = useParams();

  const navigate = useNavigate();

  let taskToUpdate = null;

  if (params.id) {
    taskToUpdate = useSelector((state) =>
      state.tasks.tasks.find((task) => task.id == params.id)
    );
  }

  useEffect(() => {
    if (taskToUpdate) {
      setDescription(taskToUpdate.description);
      setListId(taskToUpdate.listId);
    }
  }, [taskToUpdate]);

  useLayoutEffect(() => {
    document.title = `${params.id ? "Actualizar" : "Adicionar"} Entrada`;
  });

  const isFormValid = () => {
    let isValid = true;

    if (listId == "") {
      setErrorListId("Selecione uma lista");
      isValid = false;
    } else {
      setErrorListId("");
    }

    if (description == "") {
      setErrorDescription("Introduza uma tarefa");
      isValid = false;
    } else {
      setErrorDescription("");
    }

    return isValid;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!isFormValid()) return;

    const task = {
      listId,
      description,
    };

    if (params.id) {
      dispatch(updateTask(params.id, task));
    } else {
      dispatch(addTask(task));

      const progress = calculateProgress(listId, allTasks);

      dispatch(updateProgress(listId, progress));
    }

    navigate(-1);
  };

  return (
    <div className="container">
      <form className="form" onSubmit={handleSubmit}>
        <div className="form__group">
          <label htmlFor="list" className="form__label">
            Lista
          </label>
          <select
            value={listId}
            name="list"
            id="list"
            className="form__select"
            onChange={(e) => setListId(e.target.value)}
          >
            <option value="">-- Selecione uma lista --</option>
            {lists.map((list, i) => (
              <option key={i} value={list.id}>
                {list.designation}
              </option>
            ))}
          </select>
          <span className="form__error">{errorListId}</span>
        </div>
        <div className="form__group">
          <label htmlFor="description" className="form__label">
            Entrada
          </label>
          <input
            type="text"
            className="form__input"
            aria-label="Entrada"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
          <span className="form__error">{errorDescription}</span>
        </div>
        <div className="form__group">
          <button className="btn btn--primary">
            {params.id ? "Actualizar" : "Adicionar"}
          </button>
        </div>
      </form>
    </div>
  );
};

export default AddToInbox;



