export const calculateProgress = (listId, allTasks, projectId) => {
  let progress = 0;

  let numTasksDone = 0;

  let tasksFrom = [];

  if (listId) {
    tasksFrom = allTasks.filter((task) => task.listId == listId);
  } else {
    tasksFrom = allTasks.filter((task) => task.projectId == projectId);
  }

  tasksFrom.forEach((task) => {
    if (task.done) {
      numTasksDone++;
    }
  });

  progress = Math.floor((numTasksDone / tasksFrom.length) * 100);

  return progress;
};



