import React from "react";
import { useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { setCurrentUser } from "../redux/actions/user.actions";

const SidebarLayout = (props) => {
  //const currentUser = useSelector((state) => state.user.currentUser);
  const currentUser = true;

  const dispatch = useDispatch();

  const navigate = useNavigate();

  const user = null;
  const token = null;
  return (
    <nav className={`sidebar `}>
      <Link to={currentUser ? "/home" : "/signin"} className="sidebar__brand">
        Kwanga
      </Link>
      {currentUser ? (
        <ul className="sidebar__items">
          <li className="sidebar__item">
            <Link to="/inbox" className="sidebar__link">
              Entradas
            </Link>
          </li>
          <li className="sidebar__item">
            <Link to="/home" className="sidebar__link">
              Proximas Accoes
            </Link>
          </li>
          <li className="sidebar__item">
            <Link to="anual-goals" className="sidebar__link">
              Objectivos Anuais
            </Link>
          </li>
          <li className="sidebar__item">
            <Link to="monthly-goals" className="sidebar__link">
              Objectivos Mensais
            </Link>
          </li>
          <li className="sidebar__item">
            <Link to="projects" className="sidebar__link">
              Gerir Projectos
            </Link>
          </li>
          <li className="sidebar__item">
            <Link to="life-fields" className="sidebar__link">
              Areas da Vida
            </Link>
          </li>
          <li className="sidebar__item">
            <Link to="lists" className="sidebar__link">
              Listas
            </Link>
          </li>
          <li className="sidebar__item sidebar__item--bottom">
            <div
              onClick={() => {
                dispatch(setCurrentUser({ user, token }));

                navigate("/login", { replace: true });
              }}
              className="sidebar__link"
            >
              Sair
            </div>
          </li>
        </ul>
      ) : (
        <ul className="sidebar__items">
          <li className="sidebar__item">
            <Link to="signin" className="sidebar__link">
              Entrar
            </Link>
          </li>
          <li className="sidebar__item">
            <Link to="signup" className="sidebar__link">
              Registar
            </Link>
          </li>
        </ul>
      )}
    </nav>
  );
};

export default SidebarLayout;



