import React, { useLayoutEffect, useState } from "react";
import API_URL from "./../../config/api-config";
import spinnerUtil from "../../utils/spinner.util";
import { useDispatch } from "react-redux";
import { setCurrentUser } from "../../redux/actions/user.actions";
import { useNavigate } from "react-router-dom";

const SignUpPage = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");

  const [errorName, setErrorName] = useState("");
  const [errorEmail, setErrorEmail] = useState("");
  const [errorPassword, setErrorPassword] = useState("");
  const [errorPasswordConfirm, setErrorPasswordConfirm] = useState("");

  const dispatch = useDispatch();

  const navigate = useNavigate();

  const isFormValid = () => {
    let isValid = true;

    if (name === "") {
      setErrorName("Nome e obrigatorio");
      isValid = false;
    } else {
      setErrorName("");
    }

    if (email === "") {
      setErrorEmail("Email e obrigatorio");
      isValid = false;
    } else {
      setErrorEmail("");
    }

    if (password === "") {
      setErrorPassword("Senha e obrigatorio");
      isValid = false;
    } else {
      setErrorPassword("");
    }

    if (passwordConfirm === "") {
      setErrorPasswordConfirm("Confirmacao da senha e obrigatorio");
      isValid = false;
    } else {
      setErrorPasswordConfirm("");
    }

    if (password.length < 8) {
      setErrorPassword("A senha deve ter no minimo 8 caracteres");
      isValid = false;
    } else {
      setErrorPassword("");
    }

    if (password !== passwordConfirm) {
      setErrorPasswordConfirm("As senhas nao sao iguais");
      isValid = false;
    } else {
      setErrorPasswordConfirm("");
    }

    return isValid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!isFormValid()) return;

    spinnerUtil.showSpinner();

    try {
      const response = await fetch(`${API_URL}/users/signup`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ name, email, password, passwordConfirm }),
      });

      const data = await response.json();

      if (data.status === "success") {
        const user = data.data.user;

        const token = data.token;

        dispatch(setCurrentUser({ user, token }));

        navigate("/home", { replace: true });
      } else {
      }

      console.log(data);
    } catch (err) {
      alert(err);
    } finally {
      spinnerUtil.hideSpinner();
    }
  };

  useLayoutEffect(() => {
    document.title = "Registar";
  });

  return (
    <div className="login container">
      <form className="form login__form" onSubmit={handleSubmit}>
        <div className="form__group">
          <label htmlFor="name" className="form__label">
            Nome
          </label>
          <input
            type="text"
            className="form__input"
            placeholder="Nelson"
            aria-label="Name"
            value={name}
            onChange={(e) => {
              setName(e.target.value);
            }}
          />
          <span className="form__error">{errorName}</span>
        </div>
        <div className="form__group">
          <label htmlFor="email" className="form__label">
            Email
          </label>
          <input
            type="email"
            className="form__input"
            placeholder="tu@exemplo.com"
            value={email}
            onChange={(e) => {
              setEmail(e.target.value);
            }}
          />
          <span className="form__error">{errorEmail}</span>
        </div>

        <div className="form__group">
          <label htmlFor="password" className="form__label">
            Senha
          </label>
          <input
            type="password"
            className="form__input"
            aria-label="Password"
            value={password}
            onChange={(e) => {
              setPassword(e.target.value);
            }}
          />
          <span className="form__error">{errorPassword}</span>
        </div>

        <div className="form__group">
          <label htmlFor="passwordConfirm" className="form__label">
            Confirmar Senha
          </label>
          <input
            type="password"
            className="form__input"
            aria-label="Confirmar Password"
            value={passwordConfirm}
            onChange={(e) => {
              setPasswordConfirm(e.target.value);
            }}
          />
          <span className="form__error">{errorPasswordConfirm}</span>
        </div>
        <div className="form__group">
          <button type="submit" className="btn btn--primary form__btn">
            Registar
          </button>
        </div>
      </form>
    </div>
  );
};

export default SignUpPage;



