import ListsTypes from "../types/lists.types";

export const createList = (list) => ({
  type: ListsTypes.CREATE,
  payload: list,
});

export const updateList = (id, list) => ({
  type: ListsTypes.UPDATE,
  payload: { id, list },
});

export const deleteList = (id) => ({
  type: ListsTypes.DELETE,
  payload: id,
});

export const updateProgress = (id, progress) => ({
  type: ListsTypes.UPDATE_PROGRESS,
  payload: { id, progress },
});

export const changeListVisibility = (id) => ({
  type: ListsTypes.CHANGE_LIST_VISIBILITY,
  payload: id,
});



