import React, { useState, useLayoutEffect, useEffect } from "react";
import Overview from "../../components/overview.component";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import GoalItem from "../../components/goal-item.component";
import modalUtil from "../../utils/modal.util";
import { deleteProject } from "../../redux/actions/projects.actions";
import { useNavigate } from "react-router";
import { updateProjectProgress } from "../../redux/actions/projects.actions";
import SelectMonth from "../../components/select-month.component";
const ProjectsPage = () => {
  const date = new Date();

  const [year, setYear] = useState(date.getFullYear());
  const [month, setMonth] = useState(date.getMonth());
  const [percentage, setPercentage] = useState(0);

  const lifeFields = useSelector((state) => state.lifeFields.fields);

  const anualGoals = useSelector((state) => state.anualGoals.goals);

  const monthlyGoals = useSelector((state) => state.monthlyGoals.goals);

  const projects = useSelector((state) => state.projects.projects);

  const dispatch = useDispatch();

  const navigate = useNavigate();

  const projectsArr = {};

  lifeFields.map((field) => {
    projectsArr[field.designation] = [];

    anualGoals.map((anualGoal) => {
      monthlyGoals.map((monthlyGoal) => {
        const project = projects.filter((project) => {
          if (
            project.monthlyGoalId == monthlyGoal.id &&
            anualGoal.lifeFieldId == field.id &&
            anualGoal.id == monthlyGoal.anualGoalId &&
            anualGoal.year == year &&
            monthlyGoal.month == month
          ) {
            project.montlhyGoalDescription = monthlyGoal.description;

            return project;
          }
        });

        if (project.length > 0) {
          projectsArr[field.designation].push(...project);
        }
      });
    });
  });

  const handleAddTasks = (id) => {
    navigate(`/projects/add-tasks/${id}`);
  };

  const handleEdit = (id) => {
    navigate(`/projects/update/${id}`);
  };

  const handleDelete = (id) => {
    const title = "Confirmar Remocao";
    const msg = "Tem certeza que deseja remover este registo?";

    modalUtil.showModal(title, msg, () => {
      dispatch(deleteProject(id));
    });
  };

  useLayoutEffect(() => {
    document.title = "Projectos ";
  });

  useEffect(() => {}, []);

  return (
    <div>
      <Overview
        circularProgressBarTitle={`Projectos`}
        circularProgressBarPercentage={percentage}
      />
      <div className="container">
        <Link to="/projects/create" className="btn btn--primary btn--block">
          Novo
        </Link>
        <form className="form mt-2">
          <div className="form__group">
            <label htmlFor="year" className="form__label">
              Ano
            </label>
            <select
              name="year"
              id="year"
              className="form__select"
              onChange={(e) => setYear(e.target.value)}
              value={year}
            >
              <option value="2022">2022</option>
              <option value="2021">2021</option>
              <option value="2020">2020</option>
            </select>
          </div>

          <div className="form__group">
            <label htmlFor="month" className="form__label">
              M&ecirc;s
            </label>
            <SelectMonth
              label="month"
              value={month}
              onChange={(e) => setMonth(e.target.value)}
            />
          </div>
        </form>
        <div className="goals">
          {lifeFields.map((field, i) => {
            return (
              <div key={i} className="goals__section mt-3">
                <div className="goals__header">
                  <div className="goals__section-title">
                    {field.designation}
                  </div>
                </div>
                <ul className="goals__list">
                  {projectsArr[field.designation].map((project, j) => {
                    return (
                      <GoalItem
                        key={j}
                        id={project.id}
                        description={project.title}
                        progress={project.progress}
                        onDeleteItem={() => handleDelete(project.id)}
                        onEditItem={() => handleEdit(project.id)}
                        onAddTasks={() => handleAddTasks(project.id)}
                        isProject={true}
                        parent={project.montlhyGoalDescription}
                      />
                    );
                  })}
                </ul>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default ProjectsPage;



