import React, { useState } from "react";
import SubMenu from "./sub-menu.component";

const Menu = ({
  showMenu,
  onDelete,
  onMoveUp,
  onMoveDown,
  onMoveToList,
  onEdit,
  numSelectedItems,
  canSubmenuEdit,
}) => {
  const [showSubmenu, setShowSubmenu] = useState(false);

  return (
    <React.Fragment>
      {showMenu ? <div className="height-5"></div> : null}
      <ul className={`menu ${showMenu ? "menu--show" : ""}`}>
        <div className="menu__container">
          <li
            className={`menu__item ${
              numSelectedItems > 1 ? "menu__item--disabled" : ""
            }`}
            onClick={onMoveUp}
          >
            <img
              src={require("./../../public/assets/icons/arrow-bold-up.svg")}
              className="menu__icon"
              alt="Mover para cim"
            />
            <span className="menu__text">Subir</span>
          </li>
          <li
            className={`menu__item ${
              numSelectedItems > 1 ? "menu__item--disabled" : ""
            }`}
            onClick={onMoveDown}
          >
            <img
              src={require("./../../public/assets/icons/arrow-down.svg")}
              className="menu__icon"
              alt="Mover para cim"
            />
            <span className="menu__text">Descer</span>
          </li>
          <li className={`menu__item`} onClick={onMoveToList}>
            <img
              src={require("./../../public/assets/icons/forward.svg")}
              className="menu__icon"
              alt="Mover para lista"
            />
            <span className="menu__text">Mover</span>
          </li>
          <li className="menu__item" onClick={onDelete}>
            <img
              src={require("./../../public/assets/icons/trash.svg")}
              className="menu__icon"
              alt="Mover para cim"
            />
            <span className="menu__text">Eliminar</span>
          </li>
          <li
            className="menu__item menu__item--more"
            onClick={() => setShowSubmenu((currentValue) => !currentValue)}
          >
            <img
              src={require("./../../public/assets/icons/dots-three-vertical.svg")}
              className="menu__icon"
              alt="Mover para cim"
            />
            <span className="menu__text">Mais</span>
            <SubMenu
              canSubmenuEdit={canSubmenuEdit}
              onEdit={onEdit}
              showSubmenu={showSubmenu}
              numSelectedItems={numSelectedItems}
            />
          </li>
        </div>
      </ul>
    </React.Fragment>
  );
};

export default Menu;



