import ListsTypes from "../types/lists.types";
import List from "./../../models/list.model";
import LISTS_DATA from "../../data/lists.data";
const INITIAL_STATE = {
  lists: LISTS_DATA,
};

const listsReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case ListsTypes.CREATE: {
      const newList = new List(action.payload.designation, action.payload.type);

      return {
        ...state,
        lists: [...state.lists, newList],
      };
    }
    case ListsTypes.UPDATE: {
      const index = state.lists.findIndex((field) => {
        return field.id == action.payload.id;
      });

      const updatedLists = Array.from(state.lists);

      const updatedList = updatedLists[index];

      updatedList.type = action.payload.list.type;
      updatedList.designation = action.payload.list.designation;

      updatedLists.splice(index, 1, updatedList);

      return {
        ...state,
        lists: updatedLists,
      };
    }
    case ListsTypes.DELETE: {
      const index = state.lists.findIndex((field) => {
        return field.id == action.payload;
      });

      const updatedLists = Array.from(state.lists);

      updatedLists.splice(index, 1);

      return {
        ...state,
        lists: updatedLists,
      };
    }
    case ListsTypes.UPDATE_PROGRESS: {
      const index = state.lists.findIndex((list) => {
        return list.id == action.payload.id;
      });

      const updatedLists = Array.from(state.lists);

      const updatedList = updatedLists[index];

      updatedList.progress = action.payload.progress;
      updatedLists.splice(index, 1, updatedList);

      return {
        ...state,
        lists: updatedLists,
      };
    }
    case ListsTypes.CHANGE_LIST_VISIBILITY: {
      const index = state.lists.findIndex((list) => list.id == action.payload);

      const currentLists = Array.from(state.lists);

      const list = currentLists[index];

      list.isVisible = !list.isVisible;

      currentLists.splice(index, 1, list);

      return {
        ...state,
        lists: currentLists,
      };
    }
    default:
      return state;
  }
};

export default listsReducer;



