import UserTypes from "../types/user.types";
const INITIAL_STATE = {
  currentUser: null,
  token: null,
};

const userReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case UserTypes.SET_CURRENT_USER:
      return {
        ...state,
        currentUser: action.payload.user,
        token: action.payload.token,
      };

    default:
      break;
  }
  return state;
};

export default userReducer;



