import TasksTypes from "./../types/tasks.types";

export const addTask = (task) => ({
  type: TasksTypes.CREATE,
  payload: task,
});

export const addProjectTasks = (tasks) => ({
  type: TasksTypes.CREATE_PROJECT_TASKS,
  payload: tasks,
});

export const updateTask = (id, task) => ({
  type: TasksTypes.UPDATE,
  payload: { id, task },
});

export const deleteTask = (id) => ({
  type: TasksTypes.DELETE,
  payload: id,
});

export const deleteManyTasks = (ids) => ({
  type: TasksTypes.DELETE_MANY,
  payload: ids,
});

export const changeTaskStatus = (id) => ({
  type: TasksTypes.CHANGE_STATUS,
  payload: id,
});

export const moveTaskUp = (id) => ({
  type: TasksTypes.MOVE_TASK_UP,
  payload: id,
});

export const moveTaskDown = (id) => ({
  type: TasksTypes.MOVE_TASK_DOWN,
  payload: id,
});

export const moveTasksToList = (ids, listId) => ({
  type: TasksTypes.MOVE_TASKS_TO_LIST,
  payload: { ids, listId },
});



