class AnualGoalModel {
  constructor(year, lifeFieldId, description, progress = 0) {
    this.id = Date.now();
    this.year = +year;
    this.lifeFieldId = lifeFieldId;
    this.description = description.trim();
    this.progress = progress;
    this.createdAt = new Date().toUTCString();
  }
}

export default AnualGoalModel;



