import LifeFieldsTypes from "../types/life-fields.types";

export const createLifeField = (designation) => ({
  type: LifeFieldsTypes.CREATE_LIFE_FIELD,
  payload: designation,
});

export const updateLifeField = (id, designation) => ({
  type: LifeFieldsTypes.UPDATE_LIFE_FIELD,
  payload: { id, designation },
});

export const deleteLifeField = (id) => ({
  type: LifeFieldsTypes.DELETE_LIFE_FIELD,
  payload: id,
});

export const changeLifeFieldVisibility = (id) => ({
  type: LifeFieldsTypes.CHANGE_LIFE_FIELD_VISIBILITY,
  payload: id,
});



