import React, { useLayoutEffect } from "react";
import { useNavigate } from "react-router-dom";

const ResetTokenPage = () => {
  useLayoutEffect(() => {
    document.title = "Senha enviada!";
  });
  const navigate = useNavigate();

  return (
    <div className="container">
      <div className="form__group">
        <p className="p">
          Verifique seu e-mail para um link para redefinir sua senha. Se não
          aparecer em alguns minutos, verifique sua pasta de spam.
        </p>
      </div>
      <div className="form__group">
        <button
          onClick={() => navigate("/signin")}
          type="submit"
          className="btn btn--primary form__btn"
        >
          Voltar para Entrar
        </button>
      </div>
    </div>
  );
};

export default ResetTokenPage;



