import AnualGoalsTypes from "../types/anual-goals.types";
import AnualGoalModel from "./../../models/anual-goal.model";
const INITIAL_STATE = {
  goals: [],
};

const anualGoalReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case AnualGoalsTypes.CREATE:
      const newAnualGoal = new AnualGoalModel(
        action.payload.year,
        action.payload.lifeField,
        action.payload.description
      );

      return {
        ...state,
        goals: state.goals.concat(newAnualGoal),
      };
    case AnualGoalsTypes.UPDATE: {
      const index = state.goals.findIndex(
        (goal) => goal.id == action.payload.id
      );

      const updatedGoalsArr = Array.from(state.goals);

      const updatedGoal = updatedGoalsArr[index];

      updatedGoal.year = action.payload.anualGoal.year;
      updatedGoal.lifeFieldId = action.payload.anualGoal.lifeField;
      updatedGoal.description = action.payload.anualGoal.description;

      updatedGoalsArr.splice(index, 1, updatedGoal);

      return {
        ...state,
        goals: updatedGoalsArr,
      };
    }
    case AnualGoalsTypes.DELETE: {
      const index = state.goals.findIndex((goal) => goal.id == action.payload);

      const updatedGoalsArr = Array.from(state.goals);

      updatedGoalsArr.splice(index, 1);

      return {
        ...state,
        goals: updatedGoalsArr,
      };
    }
    default:
      return state;
  }
};

export default anualGoalReducer;



