import AnualGoalsTypes from "../types/anual-goals.types";

export const createAnualGoal = (anualGoal) => ({
  type: AnualGoalsTypes.CREATE,
  payload: anualGoal,
});

export const updateAnualGoal = (id, anualGoal) => ({
  type: AnualGoalsTypes.UPDATE,
  payload: { id, anualGoal },
});

export const deleteAnualGoal = (id) => ({
  type: AnualGoalsTypes.DELETE,
  payload: id,
});



