import React, { useEffect, useLayoutEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  createLifeField,
  updateLifeField,
} from "../../redux/actions/life-fields.actions";
import { useNavigate, useParams } from "react-router";

const CreateLifeFieldPage = () => {
  const [designation, setDesignation] = useState("");
  const [errorDesignation, setErrorDesignation] = useState("");
  const params = useParams();

  let lifeField = null;

  if (params.id) {
    lifeField = useSelector((state) =>
      state.lifeFields.fields.find((field) => {
        return field.id == params.id;
      })
    );
  }

  useEffect(() => {
    if (lifeField) {
      setDesignation(lifeField.designation);
    }
  }, [lifeField]);

  useLayoutEffect(() => {
    document.title = `${params.id ? "Actualizar" : "Criar "} Area da Vida`;
  });

  const dispatch = useDispatch();

  const navigate = useNavigate();

  const isFormValid = () => {
    let isValid = true;

    if (designation === "") {
      setErrorDesignation("Este campo e obrigatorio");

      isValid = false;
    }

    return isValid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!isFormValid()) return;

    if (params.id) {
      dispatch(updateLifeField(params.id, designation));
    } else {
      dispatch(createLifeField(designation));
    }

    navigate(-1);
  };
  return (
    <div className="container">
      <form className="form" onSubmit={handleSubmit}>
        <div className="form__group">
          <label htmlFor="designation" className="form__label">
            Designa&ccedil;&atilde;o
          </label>
          <input
            type="text"
            className="form__input"
            aria-label="Descricao"
            value={designation}
            onChange={(e) => setDesignation(e.target.value)}
          />
          <span className="form__error">{errorDesignation}</span>
        </div>
        <div className="form__group">
          <button className="btn btn--primary">
            {params.id ? "Actualizar" : "Salvar"}
          </button>
        </div>
      </form>
    </div>
  );
};

export default CreateLifeFieldPage;



