import React, { useLayoutEffect, useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  createAnualGoal,
  updateAnualGoal,
} from "../../redux/actions/anual-goals.actions";
import { useNavigate, useParams } from "react-router";

const CreateAnualGoal = () => {
  const [year, setYear] = useState("");
  const [lifeField, setLifeField] = useState("");
  const [description, setDescription] = useState("");

  const [errorYear, setErrorYear] = useState("");
  const [errorLifeField, setErrorLifeField] = useState("");
  const [errorDescription, setErrorDescription] = useState("");

  const lifeFields = useSelector((state) => state.lifeFields.fields);

  const dispatch = useDispatch();

  const navigate = useNavigate();

  useLayoutEffect(() => {
    document.title = `${params.id ? "Actualizar" : "Criar"}  Objectivo Anual`;
  });

  const params = useParams();

  let anualGoal = null;

  if (params.id) {
    anualGoal = useSelector((state) =>
      state.anualGoals.goals.find((goal) => goal.id == params.id)
    );
  }

  useEffect(() => {
    if (anualGoal) {
      setYear(anualGoal.year);
      setDescription(anualGoal.description);
      setLifeField(anualGoal.lifeFieldId);
    }
  }, [anualGoal]);

  const isFormValid = () => {
    let isValid = true;

    if (year === "") {
      setErrorYear("Este campo e obrigatorio");
      isValid = false;
    }

    if (lifeField === "") {
      setErrorLifeField("Este campo e obrigatorio");
      isValid = false;
    }

    if (description === "") {
      setErrorDescription("Este campo e obrigatorio");
      isValid = false;
    }

    return isValid;
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!isFormValid()) return;

    const anualGoal = {
      year,
      lifeField,
      description,
    };

    if (params.id) {
      dispatch(updateAnualGoal(params.id, anualGoal));
    } else {
      dispatch(createAnualGoal(anualGoal));
    }

    navigate(-1);
  };

  return (
    <div className="container">
      <form className="form" onSubmit={handleSubmit}>
        <div className="form__group">
          <label htmlFor="year" className="form__label">
            Ano
          </label>
          <select
            name="year"
            id="year"
            className="form__select"
            value={year}
            onChange={(e) => setYear(e.target.value)}
          >
            <option value="">-- Selecione o ano --</option>
            <option value="2022">2022</option>
          </select>
          <span className="form__error">{errorYear}</span>
        </div>

        <div className="form__group">
          <label htmlFor="field" className="form__label">
            Area
          </label>
          <select
            name="field"
            id="field"
            className="form__select"
            value={lifeField}
            onChange={(e) => setLifeField(e.target.value)}
          >
            <option value="">-- Selecione uma area --</option>
            {lifeFields.map((field, i) => (
              <option key={i} value={field.id}>
                {field.designation}
              </option>
            ))}
          </select>
          <span className="form__error">{errorLifeField}</span>
        </div>

        <div className="form__group">
          <label htmlFor="description" className="form__label">
            Objectivo
          </label>

          <input
            type="text"
            id="description"
            name="description"
            className="form__input"
            aria-label="description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
          <span className="form__error">{errorDescription}</span>
        </div>

        <div className="form__group">
          <button type="submit" className="btn btn--primary">
            {`${params.id ? "Actualizar" : "Criar"}`}
          </button>
        </div>
      </form>
    </div>
  );
};

export default CreateAnualGoal;



