import { combineReducers } from "redux";

import { persistReducer } from "redux-persist";

import storage from "redux-persist/lib/storage";

import listReducer from "./reducers/lists.reducer";
import lifeFieldsReducer from "./reducers/life-fields.reducer";
import anualGoalReducer from "./reducers/anual-goals.reducer";
import monthlyGoalReducer from "./reducers/monthly-goals.reducer";
import projectsReducer from "./reducers/projects.reducer";
import userReducer from "./reducers/user.reducer";
import tasksReducer from "./reducers/tasks.reducer";
const persistConfig = {
  key: "root",
  storage,
  whitelist: [
    "lifeFields",
    "anualGoals",
    "monthlyGoals",
    "projects",
    "user",
    "lists",
    "tasks",
  ],
};

const rootReducer = combineReducers({
  lists: listReducer,
  lifeFields: lifeFieldsReducer,
  anualGoals: anualGoalReducer,
  monthlyGoals: monthlyGoalReducer,
  projects: projectsReducer,
  user: userReducer,
  tasks: tasksReducer,
});

export default persistReducer(persistConfig, rootReducer);



