import React, { useLayoutEffect, useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  createMonthlyGoal,
  updateMonthlyGoal,
} from "../../redux/actions/monthly-goals.actions";
import { useParams, useNavigate } from "react-router";
import SelectMonth from "../../components/select-month.component";

const CreateMonthlyGoal = () => {
  const [month, setMonth] = useState(new Date().getMonth());
  const [anualGoalId, setAnualGoalId] = useState("");
  const [description, setDescription] = useState("");

  const [errorMonth, setErrorMonth] = useState("");
  const [errorAnualGoal, setErrorAnualGoal] = useState("");
  const [errorDescription, setErrorDescription] = useState("");

  const currentYear = new Date().getFullYear();

  const dispatch = useDispatch();

  const params = useParams();

  const navigate = useNavigate();

  const currentAnualGoals = useSelector((state) => {
    return state.anualGoals.goals.filter((goal) => goal.year == currentYear);
  });

  useLayoutEffect(() => {
    document.title = `${params.id ? "Actualizar" : "Criar"} Objectivo Mensal`;
  });

  let monthlyGoal = null;

  if (params.id) {
    monthlyGoal = useSelector((state) =>
      state.monthlyGoals.goals.find((goal) => goal.id == params.id)
    );
  }

  useEffect(() => {
    setMonth(params.month);
    setAnualGoalId(params.anualGoalId);
  }, []);

  useEffect(() => {
    if (monthlyGoal) {
      setMonth(monthlyGoal.month);
      setDescription(monthlyGoal.description);
      setAnualGoalId(monthlyGoal.anualGoalId);
    }
  }, [monthlyGoal]);

  const isFormValid = () => {
    let isValid = true;

    if (month === "") {
      setErrorMonth("Este campo e obrigatorio");
      isValid = false;
    }

    if (anualGoalId === "") {
      setErrorAnualGoal("Este campo e obrigatorio");
      isValid = false;
    }

    if (description === "") {
      setErrorDescription("Este campo e obrigatorio");
      isValid = false;
    }

    return isValid;
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!isFormValid()) return;

    const monthlyGoal = {
      month,
      anualGoalId,
      description,
    };

    if (params.id) {
      dispatch(updateMonthlyGoal(params.id, monthlyGoal));
    } else {
      dispatch(createMonthlyGoal(monthlyGoal));
    }

    setDescription("");

    navigate(-1);
  };

  return (
    <div className="container">
      <form className="form" onSubmit={handleSubmit}>
        <div className="form__group">
          <label htmlFor="anualGoalId" className="form__label">
            Objectivo Anual ({currentYear})
          </label>
          <select
            name="anualGoalId"
            id="anualGoalId"
            className="form__select"
            onChange={(e) => setAnualGoalId(e.target.value)}
            value={anualGoalId}
          >
            <option value="">-- Selecione um objectivo anual --</option>
            {currentAnualGoals.map((goal, i) => (
              <option key={i} value={goal.id}>
                {goal.description}
              </option>
            ))}
          </select>
          <span className="form__error">{errorAnualGoal}</span>
        </div>
        <div className="form__group">
          <label htmlFor="month" className="form__label">
            M&ecirc;s
          </label>
          <SelectMonth
            label="month"
            value={month}
            onChange={(e) => setMonth(e.target.value)}
          />
          <span className="form__error">{errorMonth}</span>
        </div>

        <div className="form__group">
          <label htmlFor="description" className="form__label">
            Objectivo
          </label>

          <input
            type="text"
            id="description"
            name="description"
            className="form__input"
            aria-label="description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
          <span className="form__error">{errorDescription}</span>
        </div>

        <div className="form__group">
          <button type="submit" className="btn btn--primary">
            {params.id ? "Actualizar" : "Criar"}
          </button>
        </div>
      </form>
    </div>
  );
};

export default CreateMonthlyGoal;



