import React, { useLayoutEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { deleteTask } from "../../redux/actions/tasks.actions";
import modalUtil from "../../utils/modal.util";
import { useNavigate } from "react-router";
import GoalItem from "../../components/goal-item.component";

const InboxPage = () => {
  const lists = useSelector((state) =>
    state.lists.lists.filter((list) => list.isVisible && list.type == "inbox")
  );

  const [listId, setListId] = useState(lists[0] ? lists[0].id : "");

  const tasks = useSelector((state) =>
    state.tasks.tasks.filter((task) => task.listId == listId)
  );
  useLayoutEffect(() => {
    document.title = "Entradas";
  });

  const dispatch = useDispatch();

  const navigate = useNavigate();

  const handleEdit = (id) => {
    navigate(`/inbox/update/${id}`);
  };

  const handleDelete = (id) => {
    const title = "Confirmacao Remocao";

    const msg = "Tem certeza que deseja remover esta tarefa?";

    modalUtil.showModal(title, msg, () => {
      dispatch(deleteTask(id));
    });
  };

  return (
    <React.Fragment>
      <div className="container">
        <Link to="/inbox/create" className="btn btn--primary btn--block">
          Adicionar Entrada
        </Link>

        <form className="form mt-2">
          <div className="form__group">
            <label htmlFor="list" className="form__label">
              Lista
            </label>
            <select
              value={listId}
              onChange={(e) => setListId(e.target.value)}
              name="list"
              id="list"
              className="form__select"
            >
              <option value="">-- Selecione uma lista --</option>
              {lists.map((list, i) => (
                <option key={i} value={list.id}>
                  {list.designation}
                </option>
              ))}
            </select>
          </div>
        </form>
        <ul className="goals__list">
          {tasks.map((task, j) => {
            return (
              <GoalItem
                key={j}
                id={task.id}
                description={task.description}
                progress={task.done}
                onEditItem={() => handleEdit(task.id)}
                onDeleteItem={() => handleDelete(task.id)}
                isTask={true}
              />
            );
          })}
        </ul>
      </div>
    </React.Fragment>
  );
};

export default InboxPage;



