import React, { useLayoutEffect } from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import ListItem from "../../components/list-item.component";
import { useDispatch } from "react-redux";
import { deleteLifeField } from "../../redux/actions/life-fields.actions";
import modalUtil from "./../../utils/modal.util";
import { changeLifeFieldVisibility } from "../../redux/actions/life-fields.actions";

const LifeFieldsPage = () => {
  const lifeFieldsList = useSelector((state) => state.lifeFields.fields);

  useLayoutEffect(() => {
    document.title = "Areas da Vida";
  });

  const dispatch = useDispatch();

  const handleDelete = (id) => {
    const title = "Confirmar remocao";
    const msg = "Tem certeza que deseja remover este registo?";

    modalUtil.showModal(title, msg, () => {
      dispatch(deleteLifeField(id));
    });
  };

  const handleChangeVisibility = (id) => {
    dispatch(changeLifeFieldVisibility(id));
  };

  return (
    <div className="container">
      <Link to="/create-life-field" className="btn btn--primary btn--block">
        Novo
      </Link>
      <ul className="list mt-2">
        {lifeFieldsList.map((lifeField, i) => {
          return (
            <ListItem
              key={i}
              {...lifeField}
              onDelete={handleDelete}
              updateUrl="/update-life-field"
              isVisible={lifeField.isVisible}
              onChangeVisibility={() => handleChangeVisibility(lifeField.id)}
            />
          );
        })}
      </ul>
    </div>
  );
};

export default LifeFieldsPage;



