class Spinner {
  showSpinner() {
    document
      .querySelector(".spinner__container")
      .classList.add("spinner__container--show");
    document.querySelector(".overlay").classList.add("overlay--show");
  }

  hideSpinner() {
    document
      .querySelector(".spinner__container")
      .classList.remove("spinner__container--show");
    document.querySelector(".overlay").classList.remove("overlay--show");
  }
}

export default new Spinner();



