import React, { useEffect, useLayoutEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate, useParams } from "react-router";
import {
  addProject,
  updateProject,
} from "../../redux/actions/projects.actions";
import SelectMonth from "../../components/select-month.component";

const CreateProjectPage = () => {
  const [month, setMonth] = useState("");
  const [monthlyGoalId, setMonthlyGoalId] = useState("");
  const [title, setTitle] = useState("");
  const [purpose, setPurpose] = useState("");
  const [vision, setVision] = useState("");

  const [errorMonth, setErrorMonth] = useState("");
  const [errorMonthlyGoalId, setErrorMonthlyGoalId] = useState("");
  const [errorTitle, setErrorTitle] = useState("");
  const [errorPurpose, setErrorPurpose] = useState("");
  const [errorVision, setErrorVision] = useState("");

  let goals = useSelector((state) =>
    state.monthlyGoals.goals.filter((goal) => goal.month == month)
  );

  const dispatch = useDispatch();

  const navigate = useNavigate();

  const params = useParams();

  let projectToUpdate = null;

  let monthlyGoal = null;

  if (params.id) {
    projectToUpdate = useSelector((state) =>
      state.projects.projects.find((project) => project.id == params.id)
    );

    monthlyGoal = useSelector((state) =>
      state.monthlyGoals.goals.find(
        (goal) => goal.id == projectToUpdate.monthlyGoalId
      )
    );
  }

  useEffect(() => {
    if (projectToUpdate) {
      setTitle(projectToUpdate.title);
      setPurpose(projectToUpdate.purpose);
      setVision(projectToUpdate.vision);
      setMonth(monthlyGoal.month);
      setMonthlyGoalId(projectToUpdate.monthlyGoalId);
    }
  }, [projectToUpdate]);

  const isFormValid = () => {
    let isValid = true;

    if (month == "") {
      setErrorMonth("Selecione o mes do objectivo mensal");
      isValid = false;
    } else {
      setErrorMonth("");
    }

    if (monthlyGoalId == "") {
      setErrorMonthlyGoalId("Selecione o objectivo mensal");
      isValid = false;
    } else {
      setErrorMonthlyGoalId("");
    }

    if (title == "") {
      setErrorTitle("Introduza o titulo");
      isValid = false;
    } else {
      setErrorTitle("");
    }

    if (purpose == "") {
      setErrorPurpose("Introduza o proposito");
      isValid = false;
    } else {
      setErrorPurpose("");
    }

    if (vision == "") {
      setErrorVision("Introduza a visao final");
      isValid = false;
    } else {
      setErrorVision("");
    }

    return isValid;
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!isFormValid()) return;

    const newProject = {
      title,
      purpose,
      vision,
      monthlyGoalId,
    };

    if (params.id) {
      dispatch(updateProject(params.id, newProject));
    } else {
      dispatch(addProject(newProject));
    }

    navigate(-1);
  };

  useLayoutEffect(() => {
    document.title = "Projects";
  });

  return (
    <div>
      <div className="container">
        <form className="form" onSubmit={handleSubmit}>
          <div className="form__group">
            <label htmlFor="month" className="form__label">
              Mes
            </label>
            <SelectMonth
              label="month"
              value={month}
              onChange={(e) => setMonth(e.target.value)}
            />
            <span className="form__error">{errorMonth}</span>
          </div>
          <div className="form__group">
            <label htmlFor="monthlyGoalId" className="form__label">
              Objectivo Mensal
            </label>
            <select
              name="monthlyGoalId"
              id="monthlyGoalId"
              className="form__select"
              value={monthlyGoalId}
              onChange={(e) => setMonthlyGoalId(e.target.value)}
            >
              <option value="">-- Selecione um objectivo mensal --</option>
              {goals.map((goal, i) => {
                return (
                  <option key={i} value={goal.id}>
                    {goal.description}
                  </option>
                );
              })}
            </select>
            <span className="form__error">{errorMonthlyGoalId}</span>
          </div>
          <div className="form__group">
            <label htmlFor="title" className="form__label">
              Titulo
            </label>
            <input
              type="text"
              className="form__input"
              aria-label="Titulo"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
            />
            <span className="form__error">{errorTitle}</span>
          </div>
          <div className="form__group">
            <label htmlFor="purpose" className="form__label">
              Proposito
            </label>
            <textarea
              name="purpose"
              id="purpose"
              rows="4"
              className="form__textarea"
              value={purpose}
              onChange={(e) => setPurpose(e.target.value)}
            ></textarea>
            <span className="form__error">{errorPurpose}</span>
          </div>
          <div className="form__group">
            <label htmlFor="vision" className="form__label">
              Visao
            </label>
            <textarea
              name="vision"
              id="vision"
              rows="4"
              className="form__textarea"
              value={vision}
              onChange={(e) => setVision(e.target.value)}
            ></textarea>
            <span className="form__error">{errorVision}</span>
          </div>

          <div className="form__group">
            <button className="btn btn--primary btn--block">Adicionar</button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CreateProjectPage;



