import React, { useLayoutEffect, useState } from "react";
import spinnerUtil from "../../utils/spinner.util";
import API_URL from "../../config/api-config";
import { useDispatch } from "react-redux";
import { setCurrentUser } from "../../redux/actions/user.actions";
import { useNavigate, Link } from "react-router-dom";

const SignInPage = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const [errorEmail, setErrorEmail] = useState("");
  const [errorPassword, setErrorPassword] = useState("");

  const dispatch = useDispatch();

  const navigate = useNavigate();

  useLayoutEffect(() => {
    document.title = "Entrar";
  });

  const isFormValid = () => {
    let isValid = true;

    if (email === "") {
      setErrorEmail("Email e obrigatorio");
      isValid = false;
    } else {
      setErrorEmail("");
    }

    if (password === "") {
      setErrorPassword("Senha e obrigatoria");
      isValid = false;
    } else {
      setErrorPassword("");
    }

    return isValid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!isFormValid()) return;

    spinnerUtil.showSpinner();

    try {
      const response = await fetch(`${API_URL}/users/signin`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ email, password }),
      });

      const data = await response.json();

      if (data.status === "success") {
        const user = data.data.user;

        const token = data.token;

        dispatch(setCurrentUser({ user, token }));

        navigate("/home", { replace: true });
      } else {
        setErrorEmail(data.message);
      }
    } catch (error) {
      console.log(error);
    } finally {
      spinnerUtil.hideSpinner();
    }
  };
  return (
    <div className="login container">
      <form className="form login__form" onSubmit={handleSubmit}>
        <div className="form__group">
          <label htmlFor="email" className="form__label">
            Email
          </label>
          <input
            type="email"
            className="form__input"
            placeholder="tu@exemplo.com"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <span className="form__error">{errorEmail}</span>
        </div>

        <div className="form__group">
          <label htmlFor="password" className="form__label">
            Password
          </label>
          <input
            type="password"
            className="form__input"
            aria-label="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <span className="form__error">{errorPassword}</span>
        </div>
        <div className="form__group">
          <button type="submit" className="btn btn--primary form__btn">
            Entrar
          </button>
        </div>
      </form>
      <p className="text-center">
        <Link to="/forgot-password" className="text-primary">
          Esqueci minha senha
        </Link>
      </p>
    </div>
  );
};

export default SignInPage;



