import React, { useEffect, useLayoutEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { createList, updateList } from "../../redux/actions/lists.actions";
import { useNavigate, useParams } from "react-router";

const CreateListsPage = () => {
  const [type, setType] = useState("");
  const [designation, setDesignation] = useState("");

  const [errorType, setErrorType] = useState("");
  const [errorDesignation, setErrorDesignation] = useState("");
  const params = useParams();

  let list = null;

  if (params.id) {
    list = useSelector((state) =>
      state.lists.lists.find((list) => {
        return list.id == params.id;
      })
    );
  }

  useEffect(() => {
    if (list) {
      setDesignation(list.designation);
      setType(list.type);
    }
  }, [list]);

  useLayoutEffect(() => {
    document.title = `${params.id ? "Actualizar" : "Criar "} Lista`;
  });

  const dispatch = useDispatch();

  const navigate = useNavigate();

  const isFormValid = () => {
    let isValid = true;

    if (type == "") {
      setErrorType("O tipo de lista e obrigatorio");
      isValid = false;
    }

    if (designation === "") {
      setErrorDesignation("Este campo e obrigatorio");

      isValid = false;
    }

    return isValid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!isFormValid()) return;

    const list = {
      type,
      designation,
    };

    if (params.id) {
      dispatch(updateList(params.id, list));
    } else {
      dispatch(createList(list));
    }

    navigate(-1);
  };
  return (
    <div className="container">
      <form className="form" onSubmit={handleSubmit}>
        <div className="form__group">
          <label htmlFor="type" className="form__label">
            Tipo de Lista
          </label>
          <select
            name="type"
            id="type"
            className="form__select"
            value={type}
            onChange={(e) => setType(e.target.value)}
          >
            <option value="">-- Selecione o tipo de lista --</option>
            <option value="action">Accao</option>
            <option value="inbox">Entrada</option>
          </select>
          <span className="form__error">{errorType}</span>
        </div>
        <div className="form__group">
          <label htmlFor="designation" className="form__label">
            Designa&ccedil;&atilde;o
          </label>
          <input
            type="text"
            className="form__input"
            aria-label="Descricao"
            value={designation}
            onChange={(e) => setDesignation(e.target.value)}
          />
          <span className="form__error">{errorDesignation}</span>
        </div>
        <div className="form__group">
          <button className="btn btn--primary">
            {params.id ? "Actualizar" : "Salvar"}
          </button>
        </div>
      </form>
    </div>
  );
};

export default CreateListsPage;



