import React from "react";

const Modal = () => {
  return (
    <div className="modal">
      <div className="modal__header">
        <p className="modal__title">Titulo</p>
        <img
          src={require("./../../public/assets/icons/cross.svg")}
          alt=""
          className="modal__dismiss"
        />
      </div>
      <div className="modal__body">
        <p className="modal__text">Texto aqui</p>
      </div>
      <div className="modal__footer">
        <button className="btn modal__btn modal__btn--cancel">Nao</button>
        <button className="btn modal__btn btn--primary modal__btn--submit">
          Sim
        </button>
      </div>
    </div>
  );
};

export default Modal;



