import ProjectsTypes from "../types/projects.types";
import ProjectModel from "./../../models/project.model";

const INITIAL_STATE = {
  projects: [],
};

const projectsReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case ProjectsTypes.CREATE: {
      const newProject = new ProjectModel(
        action.payload.title,
        action.payload.purpose,
        action.payload.vision,
        action.payload.monthlyGoalId
      );

      return {
        ...state,
        projects: state.projects.concat(newProject),
      };
    }
    case ProjectsTypes.UPDATE: {
      const index = state.projects.findIndex(
        (project) => project.id == action.payload.id
      );

      const updatedProjects = Array.from(state.projects);

      const updatedProject = updatedProjects[index];

      updatedProject.title = action.payload.project.title;
      updatedProject.purpose = action.payload.project.purpose;
      updatedProject.vision = action.payload.project.vision;
      updatedProject.monthlyGoalId = action.payload.project.monthlyGoalId;

      updatedProjects.splice(index, 1, updatedProject);

      return {
        ...state,
        projects: updatedProjects,
      };
    }
    case ProjectsTypes.DELETE: {
      const index = state.projects.findIndex(
        (project) => project.id == action.payload
      );

      const updatedProjects = Array.from(state.projects);

      updatedProjects.splice(index, 1);

      return {
        ...state,
        projects: updatedProjects,
      };
    }
    case ProjectsTypes.UPDATE_PROJECT_PROGRESS: {
      const index = state.projects.findIndex(
        (project) => project.id == action.payload.id
      );

      const updatedProjects = Array.from(state.projects);

      const currentProject = updatedProjects[index];

      currentProject.progress = action.payload.progress;

      return {
        ...state,
        projects: updatedProjects,
      };
    }

    default:
      return state;
  }
};

export default projectsReducer;



