import React from "react";

const SubMenu = ({ showSubmenu, onEdit, canSubmenuEdit, numSelectedItems }) => {
  return (
    <ul className={`submenu ${showSubmenu ? "submenu--show" : ""}`}>
      {canSubmenuEdit && numSelectedItems == 1 ? (
        <li className="submenu__item submenu__item--edit" onClick={onEdit}>
          Editar
        </li>
      ) : null}
    </ul>
  );
};

export default SubMenu;



