import React, { useState, useLayoutEffect, useEffect } from "react";
import modalUtil from "../../utils/modal.util";
import { useSelector, useDispatch } from "react-redux";
import {
  addTask,
  updateTask,
  deleteManyTasks,
  changeTaskStatus,
  moveTaskUp,
  moveTaskDown,
  moveTasksToList,
} from "./../../redux/actions/tasks.actions";
import { useNavigate, useParams } from "react-router";
import Menu from "./../../components/menu.component";
import GoalItem from "../../components/goal-item.component";
import { updateProjectProgress } from "../../redux/actions/projects.actions";
import { calculateProgress } from "../../utils/calculate-progress.util";

const ProjectTasksPage = () => {
  const [description, setDescription] = useState("");
  const [isEditingTask, setIsEditingTask] = useState(false);
  const [showMenu, setShowMenu] = useState(false);
  const [aux, setAux] = useState(0);
  const [selectedItems, setSelectedItems] = useState([]);
  const [taskId, setTaskId] = useState(-1);
  const [showModal, setShowModal] = useState(false);
  const [allLists, setAllLists] = useState(
    useSelector((state) => state.lists.lists)
  );

  const dispatch = useDispatch();

  const navigate = useNavigate();

  const params = useParams();

  const [tasks, setTasks] = useState([]);

  const allTasksFromProject = useSelector((state) =>
    state.tasks.tasks.filter((task) => task.projectId == params.id)
  );

  const project = useSelector((state) =>
    state.projects.projects.find((project) => project.id == params.id)
  );

  useEffect(() => {
    setTasks(allTasksFromProject);
  }, [aux]);

  useEffect(() => {
    if (selectedItems.length > 0) {
      setShowMenu(true);
    } else {
      setShowMenu(false);
    }
  }, [selectedItems]);

  const handleDelete = () => {
    const title = "Confirmar remocao";
    const msg = "Tem certeza que deseja remover esta tarefa?";

    modalUtil.showModal(title, msg, () => {
      dispatch(deleteManyTasks(selectedItems));
      setAux((currentValue) => currentValue + 1);
    });
  };

  const handleAddTask = () => {
    if (description == "") return;

    const task = {
      description,
      projectId: project.id,
    };

    if (taskId > -1) {
      dispatch(updateTask(taskId, task));
    } else {
      dispatch(addTask(task));
    }

    setTaskId(-1);

    setAux((currentValue) => ++currentValue);

    setDescription("");
  };

  const handleChangeStatus = (id) => {
    dispatch(changeTaskStatus(id));

    const progress = calculateProgress(null, allTasksFromProject, params.id);

    dispatch(updateProjectProgress(params.id, progress));
  };

  const handleMoveUp = () => {
    if (selectedItems.length > 1) return;

    const id = selectedItems[0];

    dispatch(moveTaskUp(id));

    setAux((currentValue) => currentValue + 1);
  };

  const handleMoveDown = () => {
    if (selectedItems.length > 1) return;

    const id = selectedItems[0];

    dispatch(moveTaskDown(id));

    setAux((currentValue) => currentValue + 1);
  };

  const handleMoveToList = () => {
    setShowModal(true);
  };

  const handleEdit = () => {
    if (selectedItems.length > 1) return;

    const selectedTask = tasks.find((task) => task.id == selectedItems[0]);

    setTaskId(selectedTask.id);

    setDescription(selectedTask.description);

    setSelectedItems([]);
  };

  const handleSelectItem = (newItem) => {
    const currentSelectedItems = Array.from(selectedItems);

    const index = currentSelectedItems.findIndex((item) => item == newItem);

    if (index == -1) {
      currentSelectedItems.push(newItem);
    } else {
      currentSelectedItems.splice(index, 1);
    }

    setSelectedItems(currentSelectedItems);
  };

  const isSelected = (id) => {
    const index = selectedItems.findIndex((item) => item == id);

    return index != -1;
  };

  const ListModal = ({ showModal }) => {
    const lists = useSelector((state) =>
      state.lists.lists.filter((list) => list.isVisible)
    );
    const [listId, setListId] = useState("");
    const [errorListId, setErrorList] = useState("");

    const handleSubmit = (e) => {
      e.preventDefault();

      if (listId == "") {
        setErrorList("Selecione uma lista");
        return;
      } else {
        setErrorList("");
      }

      dispatch(moveTasksToList(selectedItems, listId));

      setSelectedItems([]);

      setShowModal(false);
    };

    return (
      <React.Fragment>
        <div className={`overlay ${showModal ? "overlay--show" : null}`}></div>
        <div className={`modal ${showModal ? "modal--show" : ""}`}>
          <div className="modal__header">
            <p className="modal__title">Mover para lista</p>
          </div>
          <div className="modal__body">
            <form className="form">
              <div className="form__group">
                <label htmlFor="list" className="form__label">
                  Listas
                </label>
                <select
                  name="listId"
                  id="listId"
                  className="form__select"
                  value={listId}
                  onChange={(e) => setListId(e.target.value)}
                >
                  <option value="">-- Selecione uma list --</option>
                  {lists.map((list, i) => {
                    return (
                      <option key={i} value={list.id}>
                        {list.designation}
                      </option>
                    );
                  })}
                </select>
                <span className="form__error">{errorListId}</span>
              </div>
            </form>
          </div>
          <div className="modal__footer">
            <button
              className="btn modal__btn modal__btn--cancel"
              onClick={() => setShowModal(false)}
            >
              Nao
            </button>
            <button
              onClick={handleSubmit}
              className="btn modal__btn btn--primary modal__btn--submit"
            >
              Sim
            </button>
          </div>
        </div>
      </React.Fragment>
    );
  };

  return (
    <React.Fragment>
      <ListModal showModal={showModal} />
      <div className="container">
        <h4 className="h4">Projecto: {project.title}</h4>
        <form className="form mt-2">
          <div className="form__group">
            <label htmlFor="task" className="form__label">
              Tarefa
            </label>
            <div className="form__group form__group--inline">
              <input
                type="text"
                className="form__input"
                aria-label="Tarefa"
                value={description}
                onChange={(e) => setDescription(e.target.value)}
              />
              <button
                type="button"
                className="btn-image"
                onClick={handleAddTask}
              >
                <img
                  src={require("./../../../public/assets/icons/plus-5-64.png")}
                  alt="Add Task"
                  className="form__icon"
                />
              </button>
            </div>
          </div>

          <ul className="tasks__list">
            {tasks.map((task, j) => {
              const parent = allLists.find((list) => list.id == task.listId);
              return (
                <GoalItem
                  key={j}
                  id={task.id}
                  description={task.description}
                  isTask={true}
                  isAddTask={true}
                  onSelectItem={() => handleSelectItem(task.id)}
                  onChangeItemStatus={() => handleChangeStatus(task.id)}
                  done={task.done}
                  selected={isSelected(task.id)}
                  parent={parent ? parent.designation : null}
                />
              );
            })}
          </ul>
        </form>
      </div>
      <Menu
        showMenu={showMenu}
        onDelete={handleDelete}
        onMoveUp={handleMoveUp}
        onMoveDown={handleMoveDown}
        numSelectedItems={selectedItems.length}
        onMoveToList={handleMoveToList}
        canSubmenuEdit={true}
        onEdit={handleEdit}
      />
    </React.Fragment>
  );
};

export default ProjectTasksPage;



