import React, { useState, useLayoutEffect, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useParams, useNavigate } from "react-router";
import GoalItem from "../../components/goal-item.component";
import { Link } from "react-router-dom";
import modalUtil from "../../utils/modal.util";
import { deleteMonthlyGoal } from "../../redux/actions/monthly-goals.actions";
const AnualCronology = () => {
  const params = useParams();

  const navigate = useNavigate();

  const dispatch = useDispatch();

  const months = [
    "Janeiro",
    "Fevereiro",
    "Marco",
    "Abril",
    "Maio",
    "Junho",
    "Julho",
    "Agosto",
    "Setembro",
    "Outubro",
    "Novembro",
    "Dezembro",
  ];

  const anualGoal = useSelector((state) =>
    state.anualGoals.goals.find((goal) => goal.id == params.id)
  );

  const monthlyGoals = useSelector((state) =>
    state.monthlyGoals.goals.filter((goal) => goal.anualGoalId == params.id)
  );

  useLayoutEffect(() => {
    document.title = "Calendario Anual";
  }, []);

  const handleEdit = (id) => {
    navigate(`/monthly-goals/update/${id}`);
  };

  const handleDelete = (id) => {
    const title = "Confirmar remocao";
    const msg = "Tem certeza que deseja remover este objectivo?";

    modalUtil.showModal(title, msg, () => {
      dispatch(deleteMonthlyGoal(id));
    });
  };

  useEffect(() => {}, []);
  return (
    <React.Fragment>
      <div className="container">
        <div className="card">
          <h4 className="text-primary">Objectivo Anual</h4>
          <p className="text-bold mt-1"> {anualGoal.description}</p>
        </div>

        {months.map((month, i) => {
          return (
            <div key={i} className="goals__section mt-3">
              <div className="goals__header">
                <div className="goals__section-title">{month}</div>
                <div className="goals__header-right">
                  <Link
                    to={`/monthly-goals/create/${params.id}/${i}`}
                    className="btn-image"
                  >
                    <img
                      src={require("./../../../public/assets/icons/plus-5-64.png")}
                      alt="Adicionar objectivos mensais"
                      className="goals__header-icon"
                    />
                  </Link>
                </div>
              </div>
              <ul className="goals__list">
                {monthlyGoals
                  .filter((goal) => {
                    return goal.month == i;
                  })
                  .map((goal, j) => {
                    return (
                      <GoalItem
                        key={j}
                        id={goal.id}
                        description={goal.description}
                        progress={goal.progress}
                        onEditItem={() => handleEdit(goal.id)}
                        onDeleteItem={() => handleDelete(goal.id)}
                      />
                    );
                  })}
              </ul>
            </div>
          );
        })}
      </div>
    </React.Fragment>
  );
};

export default AnualCronology;



