import React from "react";
import { Link } from "react-router-dom";

const ListItem = ({
  id,
  type,
  designation,
  onDelete,
  updateUrl,
  onChangeVisibility,
  isVisible,
}) => {
  return (
    <li className="list__item">
      <div className="list__text">
        <span className="list__description">{designation}</span>
        <span className="list__type">{type}</span>
      </div>
      <div className="list__action">
        <button className="btn-image" onClick={onChangeVisibility}>
          {isVisible ? (
            <img
              src={require("./../../public/assets/icons/eye-open.svg")}
              alt=""
              className="list__action-item"
            />
          ) : (
            <img
              src={require("./../../public/assets/icons/eye-closed.svg")}
              alt=""
              className="list__action-item"
            />
          )}
        </button>
        <Link to={`${updateUrl}/${id}`} className="btn-image">
          <img
            src={require("./../../public/assets/icons/edit.svg")}
            alt=""
            className="list__action-item"
          />
        </Link>

        <button className="btn-image" onClick={() => onDelete(id)}>
          <img
            src={require("./../../public/assets/icons/cross.svg")}
            alt=""
            className="list__action-item"
          />
        </button>
      </div>
    </li>
  );
};

export default ListItem;



