class Modal {
  _handleSubmit = null;

  showModal(title, msg, fn, body = null) {
    this._handleSubmit = () => {
      fn();
      this.hideModal();
    };

    document.querySelector(".modal__title").textContent = title;

    document.querySelector(".modal__text").textContent = msg;

    if (body) {
      document.querySelector(".modal__body").innerHTML = body;
    }

    document.querySelector(".modal").classList.add("modal--show");
    document.querySelector(".overlay").classList.add("overlay--show");

    document.querySelector(".overlay").addEventListener("click", () => {
      this.hideModal();
    });

    document.querySelector(".modal__dismiss").addEventListener("click", () => {
      this.hideModal();
    });

    document
      .querySelector(".modal__btn--submit")
      .addEventListener("click", this._handleSubmit);
    document
      .querySelector(".modal__btn--cancel")
      .addEventListener("click", () => {
        this.hideModal();
      });
  }

  onSubmit() {
    this._handleSubmit;
    this.hideModal();
  }

  hideModal() {
    document.querySelector(".modal").classList.remove("modal--show");
    document.querySelector(".overlay").classList.remove("overlay--show");
    document
      .querySelector(".modal__btn--submit")
      .removeEventListener("click", this._handleSubmit);
  }
}

export default new Modal();



