class Task {
  constructor(
    description,
    listId = null,
    projectId = null,
    done = false,
    dueDate = null
  ) {
    this.id = Date.now();
    this.description = description.trim();
    this.listId = listId;
    this.projectId = projectId;
    this.dueDate = dueDate;
    this.done = done;
    this.createdAt = new Date().toUTCString();
  }
}

export default Task;



