import TasksType from "../types/tasks.types";
import Task from "./../../models/task.model";
import ListsTypes from "../types/lists.types";
const INITIAL_STATE = {
  tasks: [],
};

const tasksReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case TasksType.CREATE: {
      const newTask = new Task(
        action.payload.description,
        action.payload.listId,
        action.payload.projectId,
        false,
        action.payload.dueDate
      );

      const tasksArr = Array.from(state.tasks);

      tasksArr.push(newTask);

      return {
        ...state,
        tasks: tasksArr,
      };
    }
    case TasksType.CREATE_PROJECT_TASKS: {
      const tasksArr = Array.from(state.tasks);

      tasksArr.push(...action.payload);

      return {
        ...state,
        tasks: tasksArr,
      };
    }
    case TasksType.UPDATE: {
      const index = state.tasks.findIndex(
        (task) => task.id == action.payload.id
      );

      const allTasks = Array.from(state.tasks);

      const taskToUpdate = allTasks[index];

      taskToUpdate.description = action.payload.task.description;

      if (action.payload.task.listId)
        taskToUpdate.listId = action.payload.task.listId;
      if (action.payload.task.projectId)
        taskToUpdate.projectId = action.payload.task.projectId;

      allTasks.splice(index, 1, taskToUpdate);

      return {
        ...state,
        tasks: allTasks,
      };
    }
    case TasksType.CHANGE_STATUS: {
      const index = state.tasks.findIndex((task) => task.id == action.payload);

      const allTasks = Array.from(state.tasks);

      const taskToUpdate = allTasks[index];

      taskToUpdate.done = !taskToUpdate.done;

      allTasks.splice(index, 1, taskToUpdate);

      return {
        ...state,
        tasks: allTasks,
      };
    }
    case TasksType.DELETE: {
      const index = state.tasks.findIndex((task) => task.id == action.payload);

      const currentTasks = Array.from(state.tasks);

      currentTasks.splice(index, 1);

      return {
        ...state,
        tasks: currentTasks,
      };
    }
    case TasksType.DELETE_MANY: {
      const newTasks = state.tasks.filter((task) => {
        return action.payload.indexOf(task.id) === -1;
      });

      return {
        ...state,
        tasks: newTasks,
      };
    }

    case TasksType.MOVE_TASK_UP: {
      const currentTasks = Array.from(state.tasks);

      const index = currentTasks.findIndex((task) => task.id == action.payload);

      if (index == -1 || index == 0) return state;

      const previousTask = currentTasks[index - 1];

      const selectedTask = currentTasks[index];

      currentTasks[index] = previousTask;

      currentTasks[index - 1] = selectedTask;

      return {
        ...state,
        tasks: currentTasks,
      };
    }
    case TasksType.MOVE_TASK_DOWN: {
      const currentTasks = Array.from(state.tasks);

      const index = currentTasks.findIndex((task) => task.id == action.payload);

      if (index == -1 || index == currentTasks.length - 1) return state;

      const previousTask = currentTasks[index + 1];

      const selectedTask = currentTasks[index];

      currentTasks[index] = previousTask;

      currentTasks[index + 1] = selectedTask;

      return {
        ...state,
        tasks: currentTasks,
      };
    }
    case TasksType.MOVE_TASKS_TO_LIST: {
      const currentTasks = Array.from(state.tasks);

      const tasksIds = action.payload.ids;

      for (let i = 0; i < tasksIds.length; i++) {
        const taskToMove = currentTasks.find((task) => task.id == tasksIds[i]);

        const index = currentTasks.findIndex(
          (task) => task.id == taskToMove.id
        );

        taskToMove.listId = action.payload.listId;

        currentTasks.splice(index, 1, taskToMove);
      }

      return {
        ...state,
        tasks: currentTasks,
      };
    }
    default:
      return state;
  }
};

export default tasksReducer;



