import React, { useLayoutEffect, useState } from "react";
import { Link } from "react-router-dom";
import Overview from "../../components/overview.component";
import { useSelector, useDispatch } from "react-redux";
import GoalItem from "./../../components/goal-item.component";
import { deleteMonthlyGoal } from "../../redux/actions/monthly-goals.actions";
import modalUtil from "./../../utils/modal.util";
import { useNavigate } from "react-router";
import SelectMonth from "../../components/select-month.component";
import { getMonthName } from "../../utils/get-month-name.util";

const MonthyGoalsPage = (props) => {
  const [year, setYear] = useState(new Date().getFullYear());
  const [month, setMonth] = useState(new Date().getMonth());

  const [percentage, setPercentage] = useState(0);
  const [projects, setProjects] = useState(
    useSelector((state) => state.projects.projects)
  );

  const dispatch = useDispatch();

  const navigate = useNavigate();

  useLayoutEffect(() => {
    document.title = "Objectivos Mensais";
  });

  const calculateMonthlyProgress = () => {
    const totalProjects = projects.length;

    const totalProgress = projects.reduce((acc, project) => {
      if (!isNaN(project.progress)) return acc + project.progress;
      return acc;
    }, 0);
  };

  calculateMonthlyProgress();

  const lifeFields = useSelector((state) => state.lifeFields.fields);

  const anualGoals = useSelector((state) => state.anualGoals.goals);

  const monthlyGoals = useSelector((state) => state.monthlyGoals.goals);

  const monthlyGoalsArr = {};

  lifeFields.map((field) => {
    monthlyGoalsArr[field.designation] = [];

    anualGoals.map((anualGoal) => {
      const goals = monthlyGoals.filter((monthlyGoal) => {
        if (
          anualGoal.id == monthlyGoal.anualGoalId &&
          anualGoal.lifeFieldId == field.id &&
          monthlyGoal.month == month &&
          anualGoal.year == year
        ) {
          monthlyGoal.anualGoalDescription = anualGoal.description;
          return monthlyGoal;
        }
      });

      if (goals.length > 0) monthlyGoalsArr[field.designation].push(...goals);
    });
  });

  const handleDelete = (id) => {
    const title = "Confirmar Remocao";
    const msg = "Tem certeza que deseja remover este registo?";

    modalUtil.showModal(title, msg, () => {
      dispatch(deleteMonthlyGoal(id));
    });
  };

  const handleEdit = (id) => {
    navigate(`/monthly-goals/update/${id}`);
  };

  return (
    <React.Fragment>
      <Overview
        circularProgressBarTitle={`Mes de ${getMonthName(month)}`}
        circularProgressBarPercentage={percentage}
      />

      <div className="container">
        <Link
          to="/monthly-goals/create"
          className="btn btn--primary btn--block"
        >
          Novo
        </Link>

        <form className="form mt-2">
          <div className="form__group">
            <label htmlFor="year" className="form__label">
              Ano
            </label>
            <select
              name="year"
              id="year"
              className="form__select"
              onChange={(e) => setYear(e.target.value)}
            >
              <option value="2022">2022</option>
            </select>
          </div>

          <div className="form__group">
            <label htmlFor="month" className="form__label">
              M&ecirc;s
            </label>
            <SelectMonth
              label="month"
              value={month}
              onChange={(e) => setMonth(e.target.value)}
            />
          </div>
        </form>

        <div className="goals">
          {lifeFields.map((field, i) => {
            return (
              <div key={i} className="goals__section mt-3">
                <div className="goals__header">
                  <div className="goals__section-title">
                    {field.designation}
                  </div>
                </div>
                <ul className="goals__list">
                  {monthlyGoalsArr[field.designation].map((goal, j) => {
                    return (
                      <GoalItem
                        key={j}
                        id={goal.id}
                        description={goal.description}
                        progress={goal.progress}
                        onDeleteItem={() => handleDelete(goal.id)}
                        onEditItem={() => handleEdit(goal.id)}
                        parent={goal.anualGoalDescription}
                      />
                    );
                  })}
                </ul>
              </div>
            );
          })}
        </div>
      </div>
    </React.Fragment>
  );
};

export default MonthyGoalsPage;



