import MonthlyGoalsTypes from "../types/monthly-goals.types";

export const createMonthlyGoal = (monthlyGoal) => ({
  type: MonthlyGoalsTypes.CREATE,
  payload: monthlyGoal,
});

export const updateMonthlyGoal = (id, monthlyGoal) => ({
  type: MonthlyGoalsTypes.UPDATE,
  payload: { id, monthlyGoal },
});

export const deleteMonthlyGoal = (id) => ({
  type: MonthlyGoalsTypes.DELETE,
  payload: id,
});



