import MonthlyGoalsTypes from "../types/monthly-goals.types";
import MonthlyGoalModel from "../../models/monthly-goal.model";
const INITIAL_STATE = {
  goals: [],
};

const monthlyGoalReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case MonthlyGoalsTypes.CREATE:
      const newMonthlyGoal = new MonthlyGoalModel(
        action.payload.month,
        action.payload.description,
        action.payload.anualGoalId
      );

      return {
        ...state,
        goals: state.goals.concat(newMonthlyGoal),
      };
    case MonthlyGoalsTypes.UPDATE: {
      const index = state.goals.findIndex(
        (goal) => goal.id == action.payload.id
      );

      const updatedGoalsArr = Array.from(state.goals);

      const updatedGoal = updatedGoalsArr[index];

      updatedGoal.month = action.payload.monthlyGoal.month;
      updatedGoal.anualGoalId = action.payload.monthlyGoal.anualGoalId;
      updatedGoal.description = action.payload.monthlyGoal.description;

      updatedGoalsArr.splice(index, 1, updatedGoal);

      return {
        ...state,
        goals: updatedGoalsArr,
      };
    }
    case MonthlyGoalsTypes.DELETE: {
      const index = state.goals.findIndex((goal) => goal.id == action.payload);

      const updatedGoalsArr = Array.from(state.goals);

      updatedGoalsArr.splice(index, 1);

      return {
        ...state,
        goals: updatedGoalsArr,
      };
    }
    default:
      return state;
  }
};

export default monthlyGoalReducer;



