import React, { useEffect } from "react";
import { progress } from "../utils/circular-progress-bar";
const CircularProgressBar = (props) => {
  useEffect(() => {
    progress(props.percentage);
  });

  return (
    <div
      className={`circular-progress-bar ${
        props.size ? `circular-progress-bar--${props.size}` : ""
      }`}
    >
      <div className="circular-progress-bar__title">{props.title}</div>
      <div className="circular-progress-bar__container">
        <div className="circular-progress-bar__inner"></div>
        <div className="circular-progress-bar__number">{props.percentage}%</div>
        <div className="circular-progress-bar__circle">
          <div className="circular-progress-bar__bar circular-progress-bar--left">
            <div className="circular-progress-bar__line circular-progress-bar__line--left"></div>
          </div>
          <div className="circular-progress-bar__bar circular-progress-bar--right">
            <div className="circular-progress-bar__line  circular-progress-bar__line--right"></div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CircularProgressBar;



