import ProjectsTypes from "../types/projects.types";

export const addProject = (project) => ({
  type: ProjectsTypes.CREATE,
  payload: project,
});

export const updateProject = (id, project) => ({
  type: ProjectsTypes.UPDATE,
  payload: { id, project },
});

export const deleteProject = (id) => ({
  type: ProjectsTypes.DELETE,
  payload: id,
});

export const updateProjectProgress = (id, progress) => ({
  type: ProjectsTypes.UPDATE_PROJECT_PROGRESS,
  payload: { id, progress },
});



