import LifeFieldsTypes from "../types/life-fields.types";
import LifeField from "./../../models/life-field.model";
import LIFE_FIELDS_DATA from "../../data/life-fields.data";
const INITIAL_STATE = {
  fields: LIFE_FIELDS_DATA,
};

const lifeFieldsReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case LifeFieldsTypes.CREATE_LIFE_FIELD: {
      const newLifeField = new LifeField(action.payload);

      return {
        ...state,
        fields: [...state.fields, newLifeField],
      };
    }
    case LifeFieldsTypes.UPDATE_LIFE_FIELD: {
      const index = state.fields.findIndex((field) => {
        return field.id == action.payload.id;
      });

      const updatedLifeFields = Array.from(state.fields);

      const updatedLifeField = updatedLifeFields[index];

      updatedLifeField.designation = action.payload.designation;

      updatedLifeFields.splice(index, 1, updatedLifeField);

      return {
        ...state,
        fields: updatedLifeFields,
      };
    }
    case LifeFieldsTypes.DELETE_LIFE_FIELD: {
      const index = state.fields.findIndex((field) => {
        return field.id == action.payload;
      });

      const updatedLifeFields = Array.from(state.fields);

      updatedLifeFields.splice(index, 1);

      return {
        ...state,
        fields: updatedLifeFields,
      };
    }
    case LifeFieldsTypes.CHANGE_LIFE_FIELD_VISIBILITY: {
      const index = state.lists.findIndex(
        (field) => field.id == action.payload
      );

      const currentLifeFields = Array.from(state.fields);

      const lifeField = currentLifeFields[index];

      lifeField.isVisible = !lifeField.isVisible;

      currentLifeFields.splice(index, 1, lifeField);

      return {
        ...state,
        fields: currentLifeFields,
      };
    }
    default:
      return state;
  }
};

export default lifeFieldsReducer;



